//上方日期標題間距
.qs-controls {
    background: $gray-1;
    color: $gray-11;
    padding: 0.5em 0 0.2em 0;
    font-weight: normal;
}

//星期背景、顏色
.qs-day {
    background: $gray-3;
    border-radius: 0;
    color: $gray-11;
}

//月份的hover效果
.qs-month-year:hover {
    color: $primary-8;
    border-bottom: solid 1px $primary-8;
}

.qs-squares {
    padding: 0.5em;
}

.qs-square {
    height: 1.9em;
}

.qs-square:not(.qs-empty):not(.qs-disabled):not(.qs-day):not(.qs-active):hover {
    background: $primary-8;
    color: $gray-1;
    border-radius: 0;
}

.qs-active,
.qs-range-start,
.qs-range-end {
    background: $primary-8;
    border-radius: 0;
    color: $gray-1;
}

.qs-current {
    color: $primary-8;
    font-weight: bold;
}

.qs-overlay {
    background: $gray-1;

    .qs-overlay-month-container {
        padding: .4em 0;
    }

    .qs-overlay-year {
        color: $gray-11;
        border: solid 1px $gray-6;
        padding: 0.6em 0;
        opacity: 1;
    }

    .qs-overlay-month {
        color: $gray-11;
        opacity: 1;

        &:hover {
            color: $primary-8;
        }
    }

    .qs-submit {
        padding: .2em 1em;
        background: $primary-8;
    }

    .qs-disabled {
        background: $gray-11;

        .qs-outside-current-month {
            opacity: 1;
        }
    }

    .qs-close {
        color: $gray-11;
    }

}

.qs-right {
    &:after {
        content: "⟩";
        border: none;
    }
}

.qs-left {
    &:after {
        content: "⟨";
        border: none;
    }
}

.qs-arrow:hover {
    background: $primary-8;
    color: $gray-1;
}